/**
 * Home Component typescript file
 */
import { Component, ViewEncapsulation, ElementRef, ViewChild, HostListener } from '@angular/core';
import { ProductService } from './../services/ProductService';
import { ProductDetailsService } from './../services/ProductDetailsService';
import { ProductDetailModel } from './../models/ProductDetailModel';
import { PictureModel } from './../models/ProductDetailModel';
import { ProductAttribute } from './../models/ProductDetailModel';
import { Category } from './../models/Category';
import { Product } from './../models/Product';
import { StatusCodeEnum } from './../enums/StatusCodeEnum';
import { Modal } from 'ng2-modal';
import { KeyValuePairDTO } from './../models/KeyValuePairDTO';
import { AddToCartDTO } from './../models/AddToCartDTO';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { User } from './../models/User';
import { ShoppingCartService } from './../services/ShoppingCartService';
import { SharedService } from './../services/SharedService';
import { SigninSignupService } from './../services/SigninSignupService';

declare var jQuery: any;

@Component({
  selector: 'home',
  styles: [require('./home.component.scss')],
  template: require('./home.template.html'),
  providers: [ProductService, ProductDetailsService, ShoppingCartService, SigninSignupService, SharedService],
  encapsulation: ViewEncapsulation.None
})
export class Home {
  public router: Router;
  @ViewChild('productModal')
  private productModal: Modal;
  valuea: string;
  valueb: string;
  categories: Category[];
  topCategories: Category[] = [];
  dropdownCategories: Category[] = []
  products: Product[];
  public user: User;
  /*
  productModal
  */
  public keys: any[] = [];
  public values: any[] = [];
  public addToCartDTO: AddToCartDTO;
  public keyValuePair: KeyValuePairDTO;
  public errors: any[];

  public product: ProductDetailModel;
  productId: number = 0;
  public productprice: any;
  public producttitle: any;
  public productimages: PictureModel[] = [];
  public oldprice: any;
  public mainimage: any;
  public productdescription: any;
  public discountpercentage: number;
  public ProductAttributes: ProductAttribute;
  screensize:number;
  /*
  end
  */
  /*
  Pagination Attributes
  * */
  public pageNumber: number = 0;
  public pageSize: number = 25;
  public categoryId: number = -1;
  /**
   * end
   */
  constructor(public _productService: ProductService, private sharedService:SharedService, public _productdetail: ProductDetailsService, router: Router, public _shoppingCartService: ShoppingCartService) {
    this.user = JSON.parse(localStorage.getItem('user'));
    this.router = router;
    var self = this;



    window.onresize = function(event) {

           self.screensize = window.screen.width;
      };

    jQuery(document).ready(function () {
      jQuery(".cat-drop-triger .cat-dropdown,.cat-drop-triger").hover(function () {
         
           jQuery(".cat-drop-triger>a").css("color","white");
         
      }, function () {
        
        jQuery(".cat-drop-triger>a").css("color","#34BAC5");

      });

    })

    this._productService.getAllCategories().subscribe(
      a => {
        if (a.code == StatusCodeEnum.SUCCESS) {
          this.categories = a.data;
          var loop = 7;
          console.log( window.screen.width);
           if(window.screen.width<1050 && window.screen.width>860){
             loop = 4;   
           }

            if(window.screen.width<1350 && window.screen.width>1050){
             loop = 6;   
           }

           if(window.screen.width>1590 && window.screen.width<3000){
             loop = 14;   
           }
           if(window.screen.width>900 && window.screen.width<1050){
             loop = 4;   
           }


          for (var i = 0; i < this.categories.length; i++) {
            if (i < loop) {
              this.topCategories.push(this.categories[i]);
            }
            else {
              this.dropdownCategories.push(this.categories[i]);
            }
          }
        }
      }
    );
    this.populateProducts(this.categoryId, 0, this.pageSize);
  }

  populateProducts(categoryId, pageNumber, pageSize) {
    this._productService.getAllProducts(categoryId, pageNumber, pageSize).subscribe(
      a => {
        if (a.code == StatusCodeEnum.SUCCESS) {
          if (pageNumber == 0) {
            if (a.data.length == 0) {
              jQuery('.no-products').show();
              this.products = a.data;
            }
            else {
              jQuery('.no-products').hide();
              this.products = a.data;
            }
          }
          else {
            if (a.data.length != 0) {
              for (var i = 0; i < a.data.length; i++) {
                this.products.push(a.data[i]);
              }
            }
            else {
              jQuery('.loader-products').hide();
            }
          }
        }
      }
    );
  }

  onCategoryClick(categoryId) {
    this.categoryId = categoryId;
    this.pageNumber = 0;
    this.populateProducts(this.categoryId, 0, this.pageSize);
  }
  makeKeyValue(key, value) {
    this.keys.push(key);
    this.values.push(value);
  }
  onBuyNow() {
    jQuery('#addToCartErrors').hide();
    this.addToCartDTO = new AddToCartDTO();
    for (var i = 0; i < this.keys.length; i++) {
      this.keyValuePair = new KeyValuePairDTO();
      this.keyValuePair.keys = this.keys[i];
      this.keyValuePair.value = this.values[i];
      this.addToCartDTO.keyValuePairDtoArray.push(this.keyValuePair);
    }
    this.user = JSON.parse(localStorage.getItem('user'));
    this.sharedService.setLogged(this.user.cartCount + 1);
    this.addToCartDTO.customerId = this.user.Id;
    this.addToCartDTO.productId = this.productId;
    this.addToCartDTO.shoppingCartTypeId = 1;
    this.addToCartDTO.quantity = 1;
    this._shoppingCartService.addToCart(this.addToCartDTO).subscribe(
      a => {
        if (a.success == true) {

          jQuery("body").css('overflow', 'scroll');
          this.router.navigate(['/app/cart']);
        }
        else {
          console.log(a);
          jQuery('#addToCartErrors').show();
          this.errors = a.message;
        }
      }
    );
  }



  rightClick(event, productId) {


       var width =  $( window ).width();



    console.log(event,width);

    if(width>768){


    if (event.which == 3) {        //Right click event

      window.open('#/app/product-detail?productId=' + productId, '_newtab'); // To open in new tab
    }
    else {
      console.log(productId);
      this._productdetail.getProductDetails(productId).subscribe(
        a => {
          this.product = a.data.productDetailsModel;
          this.productId = productId;
          this.productdescription = this.product.FullDescription;
          this.productprice = this.product.ProductPrice.PriceValue;
          this.producttitle = this.product.Name;
          this.productimages = this.product.PictureModels;
          this.oldprice = this.product.ProductPrice.OldPrice;
          this.mainimage = this.productimages[0].FullSizeImageUrl;
          this.ProductAttributes = this.product.ProductAttributes;
          console.log(this.mainimage);
          
          var native_width = this.mainimage.width;
          var native_height = this.mainimage.height;
          jQuery(".large").css("background", "url('" + this.mainimage + "') no-repeat");
          //Now the mousemove function
          jQuery(".magnify").mousemove(function (e) {
            //When the user hovers on the image, the script will first calculate
            //the native dimensions if they don't exist. Only after the native dimensions
            //are available, the script will show the zoomed version.
            if (!native_width && !native_height) {
              //This will create a new image object with the same image as that in .small
              //We cannot directly get the dimensions from .small because of the 
              //width specified to 200px in the html. To get the actual dimensions we have
              //created this image object.
              var image_object = new Image();
              image_object.src = jQuery(".small").attr("src");
              //This code is wrapped in the .load function which is important.
              //width and height of the object would return 0 if accessed before 
              //the image gets loaded.
              native_width = image_object.width;
              native_height = image_object.height;
            }
            else {
              //x/y coordinates of the mouse
              //This is the position of .magnify with respect to the document.
              var magnify_offset = jQuery(this).offset();
              //We will deduct the positions of .magnify from the mouse positions with
              //respect to the document to get the mouse positions with respect to the 
              //container(.magnify)
              var mx = e.pageX - magnify_offset.left;
              var my = e.pageY - magnify_offset.top;

              //Finally the code to fade out the glass if the mouse is outside the container
              if (mx < jQuery(this).width() && my < jQuery(this).height() && mx > 0 && my > 0) {
                $(".large").fadeIn(100);
              }
              else {
                $(".large").fadeOut(100);
              }
              if ($(".large").is(":visible")) {
                //The background position of .large will be changed according to the position
                //of the mouse over the .small image. So we will get the ratio of the pixel
                //under the mouse pointer with respect to the image and use that to position the 
                //large image inside the magnifying glass
                var rx = Math.round(mx / jQuery(".small").width() * native_width - jQuery(".large").width() / 2) * -1;
                var ry = Math.round(my / jQuery(".small").height() * native_height - jQuery(".large").height() / 2) * -1;
                var bgp = rx + "px " + ry + "px";
                //Time to move the magnifying glass with the mouse
                var px = mx - jQuery(".large").width() / 2;
                var py = my - jQuery(".large").height() / 2;
                //Now the glass moves with the mouse
                //The logic is to deduct half of the glass's width and height from the 
                //mouse coordinates to place it with its center at the mouse coordinates

                //If you hover on the image now, you should see the magnifying glass in action
                jQuery(".large").css({ left: px, top: py, backgroundPosition: bgp });
              }
            }
          })

        this.discountpercentage=a.data.discountPercentage
          jQuery(".prod-detail-modal").fadeIn();
          jQuery("body").css('overflow', 'hidden');
        }
      );
    }




    }else{
        window.open('#/app/product-detail?productId=' + productId, '_newtab'); // To open in new tab
    }

  }


  onWishList() {
    jQuery('#addToCartErrors').hide();
    this.addToCartDTO = new AddToCartDTO();
    this.user = JSON.parse(localStorage.getItem('user'));
    this.addToCartDTO.customerId = this.user.Id;
    this.addToCartDTO.productId = this.productId;
    this.addToCartDTO.shoppingCartTypeId = 2;
    this.addToCartDTO.quantity = 1;
    this._shoppingCartService.addToCart(this.addToCartDTO).subscribe(
      a => {
        if (a.success == true) {
          console.log('Added to Wish List');
          //this.router.navigate(['/app/cart']);
        }
        else {
          console.log(a);
          jQuery('#addToCartErrors').show();
          this.errors = a.message;
        }
      }
    );
  }

  ngOnInit(): void {
    var self = this;
    jQuery("body").on("click", ".close-modal", function (e) {
      self.errors = [];
      jQuery('#addToCartErrors').hide();
      var $this = jQuery(this);
      e.preventDefault();
      jQuery(this).parents(".prod-detail-modal").fadeOut();
      jQuery("body").css('overflow', 'scroll');
      jQuery(".zoomContainer").html("");
    });

  }
  @HostListener("window:scroll", ["$event"])
  onWindowScroll() {
    //In chrome and some browser scroll is given to body tag
    let pos = (document.documentElement.scrollTop || document.body.scrollTop) + document.documentElement.offsetHeight;
    let max = document.documentElement.scrollHeight;
    // pos/max will give you the distance between scroll bottom and and bottom of screen in percentage.
    if (pos == max) {
      console.log('Pagination Called');
      this.pageNumber = this.pageNumber + 1;
      this.populateProducts(this.categoryId, this.pageNumber, this.pageSize);
    }
  }
  changeimage(image: any) {
    this.mainimage = image;
var native_width = this.mainimage.width;
          var native_height = this.mainimage.height;
         jQuery(".large").css("background", "url('" + this.mainimage + "') no-repeat");
          //Now the mousemove function
          jQuery(".magnify").mousemove(function (e) {
            //When the user hovers on the image, the script will first calculate
            //the native dimensions if they don't exist. Only after the native dimensions
            //are available, the script will show the zoomed version.
            if (!native_width && !native_height) {
              //This will create a new image object with the same image as that in .small
              //We cannot directly get the dimensions from .small because of the 
              //width specified to 200px in the html. To get the actual dimensions we have
              //created this image object.
              var image_object = new Image();
              image_object.src = jQuery(".small").attr("src");
              //This code is wrapped in the .load function which is important.
              //width and height of the object would return 0 if accessed before 
              //the image gets loaded.
              native_width = image_object.width;
              native_height = image_object.height;
            }
            else {
              //x/y coordinates of the mouse
              //This is the position of .magnify with respect to the document.
              var magnify_offset = jQuery(this).offset();
              //We will deduct the positions of .magnify from the mouse positions with
              //respect to the document to get the mouse positions with respect to the 
              //container(.magnify)
              var mx = e.pageX - magnify_offset.left;
              var my = e.pageY - magnify_offset.top;

              //Finally the code to fade out the glass if the mouse is outside the container
              if (mx < jQuery(this).width() && my < jQuery(this).height() && mx > 0 && my > 0) {
                $(".large").fadeIn(100);
              }
              else {
                $(".large").fadeOut(100);
              }
              if ($(".large").is(":visible")) {
                //The background position of .large will be changed according to the position
                //of the mouse over the .small image. So we will get the ratio of the pixel
                //under the mouse pointer with respect to the image and use that to position the 
                //large image inside the magnifying glass
                var rx = Math.round(mx / jQuery(".small").width() * native_width - jQuery(".large").width() / 2) * -1;
                var ry = Math.round(my / jQuery(".small").height() * native_height - jQuery(".large").height() / 2) * -1;
                var bgp = rx + "px " + ry + "px";
                //Time to move the magnifying glass with the mouse
                var px = mx - jQuery(".large").width() / 2;
                var py = my - jQuery(".large").height() / 2;
                //Now the glass moves with the mouse
                //The logic is to deduct half of the glass's width and height from the 
                //mouse coordinates to place it with its center at the mouse coordinates

                //If you hover on the image now, you should see the magnifying glass in action
                jQuery(".large").css({ left: px, top: py, backgroundPosition: bgp });
              }
            }
          })
  }
}



